import React, { useState, useEffect } from "react";
import { Segment, Icon } from "semantic-ui-react";

import API from "../api";

const sampleEvent = {
  type: "Water Distribution",
  description: "",
  distance: 250,
  lastActive: "Just now",
  icon: "first-aid-kit",
  color: "gainsboro",
};

const EventDetails = ({}) => {
  const [event, setEvent] = useState(null);

  useEffect(() => {
    const load = async () => {
      const result = await API.post("/vieweventdetails", {
        eventId: "D0B83D0C-0C96-46F5-8313-40FA9C6AA1D6",
      });
      console.log(result);
    };

    load();
  }, []);

  return (
    <Segment raised inverted style={{ padding: 8, paddingBottom: 44 }}>
      <div style={{ height: 200 }} />
    </Segment>
  );
};

export default EventDetails;
